<template>
  <div class="boxCadastrarAviso">
    <v-container mt-1 mr-5>
      <v-flex>
        <h1 v-if="avisoEditar">Editar Aviso</h1>
        <h1 v-else>Cadastrar Aviso</h1>
      </v-flex>
      <v-layout row>
        <v-flex md8>
          <v-layout d-flex mt-4>
            <v-flex md10 class="white lighten-2">
              <v-form class="ml-4">
                <v-flex md12 mt-3>
                  <h1 class="input">Titulo</h1>
                  <v-text-field
                    name="titulo"
                    v-validate="'required|max:100'"
                    :error-messages="errors.collect('titulo')"
                    data-vv-name="titulo"
                    background-color="white"
                    v-model="aviso.titulo"
                    required
                    lazy-validation
                  ></v-text-field>
                </v-flex>
                <v-flex md12 mt-3>
                  <h1 class="input">Descrio</h1>
                  <v-textarea
                    rows="10"
                    name="descricao"
                    type="text"
                    v-validate="'required'"
                    :error-messages="errors.collect('descricao')"
                    data-vv-name="descricao"
                    background-color="white"
                    box
                    v-model="aviso.descricao"
                    required
                    lazy-validation
                  ></v-textarea>
                  <v-layout row wrap justify-end>
                    <router-link :to="{path:'/'}">
                      <v-btn dark>Cancelar</v-btn>
                    </router-link>

                    <v-btn dark @click="submit()">Publicar</v-btn>
                  </v-layout>
                </v-flex>
              </v-form>
            </v-flex>
          </v-layout>
        </v-flex>
        <v-layout column align-center justify-center class="grey lighten-1" md4>
          <v-flex md2>
            <img :height="'120px'" id="base64" :src="imagemUploadUrl" />
          </v-flex>

          <v-btn color="black" dark>
            <v-flex md1>
              <input
                class="md1"
                style="opacity:0; position:absolute; float:left;"
                @change="onFileChange1"
                :type="'file'"
              />
            </v-flex>Inserir Imagem
          </v-btn>
        </v-layout>
      </v-layout>
    </v-container>
  </div>
</template>
<script>
import Aviso from "@/aviso/domain/Aviso.js";
import AvisoService from "@/aviso/services/AvisoService.js";
import Dictionary from "@/aviso/dictionary/Dictionary.js";
import Imagem from "@/shared/image/img.vue";
export default {
  components: {
    imagem: Imagem
  },

  $_veeValidate: {
    validator: "new"
  },
  mounted() {
    this.$validator.localize("pt", new Dictionary());
  },

  created() {
    this.service = new AvisoService(this.$resource);
    if (this.$session.exists()) {
      console.log(this.$session.get("avisoEditar"));
      this.aviso = this.$session.get("avisoEditar");
      // $("#base64").attr("src", "data:image/png;base64," +this.aviso.imagem);
      this.imagemUploadUrl = this.aviso.imagem;
    }
  },

  methods: {
    onFileChange1(e) {
      // this.file = e.target.files[0];
      // this.url = URL.createObjectURL(this.file);

      var f = e.target.files[0]; // FileList object

      var reader = new FileReader();
      // Closure to capture the file information.
      reader.onload = (function() {
        return function(e) {
          var binaryData = e.target.result;
          //Converting Binary Data to base 64
          var base64String = window.btoa(binaryData);
          //showing file converted to base64
          $("#base64").attr("src", "data:image/png;base64," + base64String);
          // document.getElementById("base64").value = base64String;
        };
      })(f);
      // Read in the image file as a data URL.
      reader.readAsBinaryString(f);
    },

    submit() {
      this.$validator.validateAll().then(valid => {
        if (!valid) {
          alert("Preencha os campos corretamente!");
        } else {
          //renderizando imagem
this.aviso.imagem = document.getElementById("base64").src;
          if (this.$session.exists()) {
           
            this.$http.put("avisos/" + this.aviso.id, this.aviso).then(
              () => {
                this.$session.destroy();
                
                window.location.href = "/?idAviso=avisoSuccess";

                /* this.$router.push({
                name: "home",
                query: { idAviso: "success" }
              }); */
              },
              err => {
                console.error(err);
                this.$session.destroy();
                // window.location.href = "/?idAviso=error";
                /*this.$router.push({ name: "home", query: { idAviso: "error" } })*/
              }
            );
          } else {
            //console.log(this.$http.options.root);
            console.log(this.aviso);
            this.$http.post("avisos", this.aviso).then(
              () => {
                window.location.href = "/?idAviso=avisoSuccess";
                /* this.$router.push({
                name: "home",
                query: { idAviso: "success" }
              }); */
              },
              () => {
                window.location.href = "/?idAviso=avisoError";
                /*this.$router.push({ name: "home", query: { idAviso: "error" } })*/
              }
            );
          }
        }
      });
    }
  },
  data: () => ({
    aviso: new Aviso(),
    imagemUploadUrl: require("@/assets/upload-icon.png"),
    avisoEditar: null
  })
};
</script>
<style scoped>
a {
  text-decoration: none;
  color: white;
}
</style>