export default class AvisoService {

    constructor(resource) {
        this._resource = resource('/avisos{/id}');
        this._resource1 = resource('/avisos/todos');
    }


    cadastra(aviso) {
        if (aviso.id) {
            return this._resource.update({id: aviso.id},aviso);
        } else {
            return this._resource.save(aviso);
        }


    }

    busca(){
        return this._resource1.get().then(res => res);
    }


}