<template>
  <div class="boxCadastrarAviso">
    <v-container mt-1 mr-5>
      <v-flex>
        <h1>Avisos</h1>
      </v-flex>
      <v-flex>
        <router-link :to="{path:'/cadastrarAviso'}">
          <v-btn color="black" dark>Novo Aviso</v-btn>
        </router-link>
      </v-flex>
      <v-layout row>
        <v-flex md8>
          <v-layout d-flex mt-4>
            <v-flex md10 class="white lighten-2">
              <v-form class="ml-4">
                <v-data-table :headers="headers" :items="aviso" class="elevation-1">
                  <template v-slot:items="props">
                    <tr>
                      <td>{{props.item.titulo}}</td>
                      <td>{{props.item.dataDeCriacao}}</td>
                      <td>
                        <v-icon small class="mr-2" @click="editarItem(props.item)">edit</v-icon>

                        <v-icon small @click="deleteItem(props.item.id)">delete</v-icon>
                      </td>
                    </tr>
                  </template>
                </v-data-table>
              </v-form>
            </v-flex>
          </v-layout>
        </v-flex>
      </v-layout>
    </v-container>
  </div>
</template> 
<script>
import Aviso from "@/aviso/domain/Aviso.js";
import AvisoService from "@/aviso/services/AvisoService.js";


export default {
  $_veeValidate: {
    validator: "new"
  },



  mounted() {
    this.service = new AvisoService(this.$resource);

    this.$http.get("avisos/todos").then(
      resposta => {
        this.aviso = resposta.body;
        console.log(this.aviso);
      },
      err => {
        console.error(err);
        alert("No foi possvel buscar os avisos");
      }
    );
  },

  data: () => ({
    aviso: [],
    headers: [
      { text: "Titulo", value: "titulo" },
      { text: "Data", value: "dataDeCriacao" },
      { text: "Aes", value: "acoes" }
    ]
  }),

  methods: {
    editarItem(avisoEditar) {
      this.$session.start();
      this.$session.set("avisoEditar", avisoEditar);
      window.location.href = "/cadastrarAviso";
    },

    deleteItem(id) {
      this.$http.delete('avisos/'+id).then(() =>{
        window.location.href = "/listagemAviso/?idAviso=success";
      },
      err => {
        console.err(err);
        window.location.href = "/listagemAviso/?idAviso=error";
      })
    }
  }
};
</script>
<style scoped>
a {
  text-decoration: none;
  color: white;
}
</style>