<template>
    <div class="home">
      
        <alert-success v-if="idAviso == 'avisoSuccess'" :msg ="avisoSuccess"/>
    <alert-error v-if="idAviso == 'avisoError'" :msg ="avisoError"/>
    <alert-success v-if="idAviso == 'turmaSuccess'" :msg ="turmaSuccess"/>
    <alert-error v-if="idAviso == 'turmaError'" :msg ="turmaError"/>
        <box-home/>
        <side-bar/>
          

    </div>
</template>
<script>
import SideBar from "@/shared/sideBar/SideBar.vue"
import BoxHome from "@/home/components/box/BoxHome.vue";
import AlertSuccess from "@/shared/alert/alertSuccess.vue";
import AlertError from "@/shared/alert/alertError.vue";

export default {
    components:{
