<template>
  <div class="listagemAviso">
    <alert-success v-if="idAviso == 'success'" :msg="avisoSuccess" />
    <alert-error v-if="idAviso == 'error'" :msg="avisoError" />
    <box />
    <side-bar />
  </div>
</template>
<script>
import SideBar from "@/shared/sideBar/SideBar.vue";
import BoxListagemAviso from "@/aviso/components/box/BoxListagemAviso.vue";
import AlertSuccess from "@/shared/alert/alertSuccess.vue";
import AlertError from "@/shared/alert/alertError.vue";

export default {
  components: {
    "side-bar": SideBar,
	box: BoxListagemAviso,
	'alert-success' : AlertSuccess,
    'alert-error' : AlertError,
  },
  data: () => ({
	idAviso: "",
	avisoError: "Aviso no pode ser deletado!" ,
	avisoSuccess: "Aviso Deletado com Sucesso!"
  }),

  created() {
    this.idAviso = this.$route.query.idAviso;
  }
};
</script>
